/* eslint react/prop-types: 0 */
import React, { useState } from "react";

import { Datepicker } from "@salesforce/design-system-react";

import { moment, datePickerFormatter, datePickerParser } from "../lib/utils";

const GRAXDatePicker = props => {
  const [isValidDate, setIsValidDate] = useState(true);

  return (
    <div className="slds-is-relative">
      <Datepicker
        formatter={datePickerFormatter}
        parser={datePickerParser}
        // eslint-disable-next-line
        {...props}
        onChange={(event, data) => {
          setIsValidDate(data.formattedDate === "" || moment(data.date).isValid());

          if (props.onChange != null) {
            props.onChange(event, data);
          }
        }}
      />
      {isValidDate === false ? (
        <div className="slds-text-color_error">
          The date is invalid. Please select/write a valid date.
        </div>
      ) : null}
    </div>
  );
};

export default GRAXDatePicker;
